import React from "react"
import GithubCorner from 'react-github-corner';

function Navbar() {
    const scrollToFunction = (event) => {
        var elementToScrollTo = document.querySelector(event.target.getAttribute('href'))
        console.log(event.target.getAttribute('href'));
        elementToScrollTo.scrollIntoView(true);
    }
    //flex: 0 1 auto;
    /* The above is shorthand for:
    flex-grow: 0,
    flex-shrink: 1,
    flex-basis: auto
    */
    return (
        <div style={{display:'flex', flexDirection:'row'}}>
            <a href="/" id="signatureParent" style={{flex: '0 1 200px'}}>
                <p style={{display: 'inline'}}>&#60; </p>
                <p style={{display: 'inline'}} id="signature">Guilherme Reis</p>
                <p style={{display: 'inline'}}> /&#62;</p>
            </a>
            <ul style={{flex: '1 0 400px'}} id="menu">
                <li><a onClick={scrollToFunction} href="#intro">Intro</a></li>
                <li><a onClick={scrollToFunction} href="#work">Work</a></li>
                <li><a onClick={scrollToFunction} href="#blitz">Navy Blitz</a></li>
                <li><a onClick={scrollToFunction} href="#goblins">Goblins Keep Coming</a></li>
                <li><a onClick={scrollToFunction} href="#projects">Projects</a></li>
                <li><a onClick={scrollToFunction} href="#contact">Contact</a></li>
            </ul>
            <GithubCorner style={{flex: '0 0 auto'}} href="https://github.com/guilhermereis" />
        </div>
    )
}

export default Navbar