import React from "react"

function Intro(props) {

    const scrollToFunction = (event) => {
        var elementToScrollTo = document.querySelector(event.target.getAttribute('href'))
        console.log(event.target.getAttribute('href'));
        elementToScrollTo.scrollIntoView(true);
    }
    return (
        <div id={props.id}>
            <span className="title" style={{fontWeight:'bold'}}>
                <span style={{color:'black'}}>Hi   </span><i className="fas fa-hand-paper" style={{color:'black'}}></i><span style={{color:'black'}}>   I'm Guilherme. </span>
                
                <div className="subtitle" style={{marginTop: '20px'}}>
                    <span>I've been a Unity   </span><i className="fab fa-unity"></i><span>   developer for some time now. </span>
                </div>
                <span >
                    <a className="social" href="https://www.facebook.com/guilherme.reis.169"><i className="fab fa-facebook fa-xs child" style={{margin: '0 3px 0 3px', color: '#3b5998'}}></i></a>
                    <a className="social" target="_blank" href="mailto:grrbm2@gmail.com"><i className="fas fa-envelope-square fa-xs child" style={{margin: '0 3px 0 3px', color: '#dd4b39'}}></i></a>
                    <a className="social" href="https://www.linkedin.com/in/guilherme-reis-1691b597"><i className="fab fa-linkedin-in fa-xs child" style={{margin: '0 3px 0 3px',color: '#0072b1'}}></i></a>
                    <a className="social" href="https://github.com/guilhermereis"><i className="fab fa-github fa-xs child" style={{margin: '0 3px 0 3px', color: '#211f1f'}}></i></a>
                </span>
                <div>
                    <button className="btn" href="#contact" onClick={scrollToFunction}>CONTACT ME</button>
                </div>
            </span>
            <img style={{float:'right',height:'100%', width:'55%'}} src="/imgs/manOnTable.svg" alt="Programmer" />
            
            
        </div>
    )
}

export default Intro