import React from "react"

function Project(props) {

    return (
                <a className={props.name} 
                   style={{display: 'flex', flexDirection: 'column'}}
                   target="_blank" 
                   href={props.link}>
                    <span>
                        <i className="fas fa-book fa-sm" style={{marginRight:'3px'}}></i>
                        <span style={{fontWeight:'bold',marginLeft: '0.2rem'}}>{props.title}</span>
                    </span>
                    <div style={{fontSize:'10px',flex:'1',width:'200px',margin: '5px'}}>{props.description}</div>

                    <div style={{width:'100%'}}>
                        <span style={{fontSize:'8px',float:'left', marginLeft:'5px'}}>
                            <div style={{display:'inline'}} dangerouslySetInnerHTML={{ __html: props.icon }}/>
                            <span style={{marginLeft:'3px'}}>{props.tool}</span>
                        </span>
                        <span style={{fontSize:'8px',float:'right',marginRight:'5px' }}>
                            <i className="fas fa-save fa-lg" style={{marginRight:'3px'}}></i>
                            <span>{props.size}KB</span>
                        </span>
                    </div>
                </a>
    )
}

export default Project