import React from "react"

function Work(props) {

    return (
        <div id={props.id}>
            <img style={{float:'left',height:'100%', width:'55%'}} src="/imgs/developerActivity.svg" alt="Programmer" />
            <span>
                <div className="title">
                    What I do
                </div>
                <div style={{marginTop: '20px'}} className="subtitle">
                    <span >I've worked in the industry for about two years now</span>
                    
                    <div style={{marginTop: '20px'}}>
                        <span>Developing big games and small games.</span>
                    </div>
                </div>
            </span>
            
            
        </div>
    )
}

export default Work