import React from 'react';
import YouTube from 'react-youtube';
 
class Video extends React.Component {
  
  
  render() {
    const opts = {
      height: this.props.shouldBeHeight,
      width: '640',
      playerVars: { // https://developers.google.com/youtube/player_parameters
        autoplay: 0
      }
    };
     
    return (
      <div id={this.props.id} className={this.props.class} style={{height: this.props.shouldBeHeight}}>
      <p className="title" style={{alignSelf:'flex-start',margin:'50px 0 0 50px', float:'left'}}>{this.props.title}</p>
      <p className="subtitle" style={{alignSelf:'flex-start',margin:'10px 0 50px 50px',float:'left'}}>{this.props.subtitle}</p>
      <YouTube
        videoId={this.props.url}
        opts={opts}
        onReady={this._onReady}
      />
      </div>
    );
  }
 
  _onReady(event) {
    // access to player in all event handlers via event.target
    event.target.pauseVideo();
  }
}

export default Video