import React from "react"
import Project from './Project'

function Projects(props) {

    return (
        
        <div id={props.id}>
            <p className="title">Other Projects</p>
            <div style={{display: 'flex', flexDirection: 'row'}} className="list">
                <Project 
                    name="project" 
                    title="grrbm/mustache-narwhal" 
                    description="Casual game for kids I developed for a company, you control a Narwhal and have to fish for food and avoid pollution."
                    link="https://play.google.com/store/apps/details?id=com.manimani.mustachenarwhal"
                    tool="Unity"
                    icon="<i class='fab fa-unity'></i>"
                    size="53159" />
                <Project 
                    name="project" 
                    title="grrbm/jogo-do-zilhão" 
                    description="Trivia game I developed on my spare time, based on a famous brazilian talent show."
                    link="https://play.google.com/store/apps/details?id=br.com.eusoft.jogodomilhao"
                    tool="Android Studio"
                    icon="<i style='color:#3DDC84'}} class='fab fa-android'></i>"
                    size="18233" />
                
            </div>
        </div>
    )
}

export default Projects